.features-content{
    display: flex;
    justify-content: center;

}

.features-left{
    flex: 1;
    text-align: center;

}

.features-left img{
    width: 75%;
    height: 75%;
    object-fit: cover;
    border-radius: 10px;
    margin-top: 4rem;
}

.features-right{
    flex: 1;
}

@media screen and (max-width: 600px){
    .features-content{
        flex-direction: column;
    }
}
    